#i_fiscal,
#n_empresa {
  padding: .6rem;
  border: 1px solid #b4b2b2;
  border-radius: 1rem;
}

.contenedor.hab_seleccion3 h4 {
  font-size: 2rem;
  color: #535353;
  font-weight: 600;
}

.margen_arriba {
  margin-top: 2rem;
  font-size: 1.5rem !important;
}

.hab_seleccion {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  margin: 0rem 3rem;
}

.opcion_traslado {
  font-weight: 500;
  padding-top: 2rem;
}

#pago {
  padding-top: 3rem;
}

#pago p {
  color: #4f4d4d;
  font-size: 1.6rem;
  font-weight: 500;
}

.datos_banco {
  background: #1f1f1f;
  padding: 1rem;
  border: 1px solid #c2c2c2;
  border-radius: 1.4rem;
  margin: 1rem;
  color: white !important;
  display: inline-block;
}

.btn_reservar4 a {
  color: white;
  display: inline-block;
  padding: .5rem;
  background: #ba0534;
  border-radius: 1rem;
  margin: 1rem;
}

.descrip_precio {
  display: flex;
  gap: 1rem;
}

.hab_datos_seccion {
  display: grid;
  grid-template-columns: 70% 30%;
}

.part_resumen_reserva {
  display: grid;
  grid-template-columns: 70% 30%;
  gap: 1rem;
}

.contenido-resumen {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

.reserva_comp_resumen {
  background: #b0aeae87;
  padding: 2rem;
  border-radius: 1rem;

  .part_resumen_monto {
    padding-top: 2rem;

    p {
      color: black !important;
    }

    span {
      font-weight: 700;
    }
  }
}

.boton-reserva {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn_reservar {
  border: none;
  background: $rosado;
  color: $blanco;
  padding: 1rem;
  border-radius: .3rem;
  margin: 2rem;
}

.btn_reservar:hover {
  cursor: pointer;
  background-color: #ba0534;
  transition: .2s ease-in;
}

.precio_habi {

  p:nth-child(1),
  p:nth-child(3) {
    color: $rosado;
    font-size: 1.4rem;
    text-align: center;
  }

  p:nth-child(2),
  p:nth-child(4) {
    color: $rosado;
    color: #ba0534;
    font-size: 2.9rem;
    font-weight: 600;
  }
}

.hab_part2 {
  height: 53rem;
  background-color: #E7E7E7;
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;
  box-shadow: 7px 7px 16px 0px rgba(0, 0, 0, 0.25);

  &:hover {
    transition: .5s ease;
    transform: scale(1.02);
    cursor: pointer;
  }

  .img_hab {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      height: 22rem;
      width: 92%;
    }
  }

  &.seleccionado {
    background-color: #483e44;

    p,
    h5 {
      color: $blanco !important;
    }

  }

  .info_hab {
    display: grid;
    grid-template-rows: 60% 40%;
    row-gap: 3rem;
    padding: 1rem;

    .icono_info {
      display: flex;
      gap: 5rem;
      margin-top: 1rem;
      justify-content: center;

      .icono_info_part svg {
        width: 3rem;
      }

      .tooltip {

        i,
        svg {
          color: $rosado;
          font-size: 2rem;
        }
      }
    }

    .info_hab_3 {
      .titulo_info h5 {
        color: $rosado;
        font-size: 2rem;

        &:hover {
          color: $grisMediano;
        }
      }

      .info_adicional p {
        font-size: 1.3rem;
      }

      .info_parrafo {
        width: 60%;

        p {
          font-size: 1.5rem;
          color: $grisMediano;
        }
      }
    }

  }

  .precio_hab {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.9rem;
    font-weight: 700;

    p {
      font-size: 1rem;
      text-align: center;
      color: $grisOscuro;
      margin: 0;
      padding: 0;

      .pre_nro {
        color: $fucsia;
        font-size: 2.9rem;
      }

      .des_hab {
        color: $fucsia;
        font-weight: 600;
      }
    }
  }
}

// datosss
.tit_hab_reserva_cont {
  align-items: center;
  display: flex;
  background: #3c3c3c;
  border-bottom: .3rem solid #cf5a60;
  padding: 1rem;

  img {
    width: 4rem;
  }

  h3 {
    padding: 0;
    margin: 0;
    font-size: 1.8rem;
    color: #e8e8e8;
  }

  h4 {
    padding: 0;
    margin: 0;
    font-size: 1.3rem;
    color: #e8e8e8;
    font-size: 1.3rem;
    margin: 0;
    padding: 0;
    font-weight: 400;
    color: #fff;
    font-style: italic;
  }
}

#datos_seleccion {
  background: rgba(175, 175, 175, 0.57);

  .fecha_re {
    input {
      width: 100%;
      height: 4rem;
      padding: 1rem;
      text-align: center;
      border-radius: 1rem;
      border: none;
    }

    p {
      color: #000;
      font-size: 1.5rem;
    }
  }
}

.datos_seleccion_basic {
  background: #8b8b8b;

  padding: 2rem 5rem;
}

.counter-list2 {
  background: #303030;
  padding: 1.5rem 3rem;

  .list_plan label {
    color: #fff;
    text-align: center;
  }

  .counter-btn {
    background-color: #fff0;
    border: 1px solid white;
    border-radius: 33%;
    color: #fff;
    cursor: pointer;
    height: 3rem;
    margin: auto;
    padding: 5px 10px;
    width: 3rem;
    font-weight: bolder;
  }

}

// paso 2
.contenedor.hab_seleccion3 {
  padding-top: 5rem;

  hr {
    margin-top: 3rem;
    border: 1px solid #cf5a60;
  }

  .pago_part label {
    font-weight: 600;
    color: #494747;
  }

  .metodo_pago {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 15rem;
    width: 100%;
    padding-top: 2rem;
  }

  p {
    font-size: 1.5rem;
    color: gray;
  }

  h3 {
    font-size: 3rem;
    font-weight: 400;
    color: #675b58;
  }

  #hora {
    width: 15%;
    height: 4rem;
    padding: 1rem;
    border: 1px solid #b5b2b2;
    border-radius: 1rem;
  }

  .contenedor-habitacion {
    background: #675b58;
    padding: 1rem;
    box-shadow: 9px 9px 24px -8px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 9px 9px 24px -8px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 9px 9px 24px -8px rgba(0, 0, 0, 0.75);
  }

  .contenedor-habitacion p {
    color: $blanco;
  }

  .contenedor-habitacion p:nth-child(1) {
    font-weight: 700;
    text-transform: uppercase;
  }

  textarea {
    padding: 1rem;
    overflow: auto;
    width: 100%;
    height: 24rem;
    border: 1.4px solid #b5b2b2;
    border-radius: 1rem;
    margin-bottom: 5rem;
  }
}

.part_resumen_reserva {
  .imagen_re {
    width: 100%;
    height: 100%;
    background: url(/build/img/ubi.webp);
    // background-attachment: fixed;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;

  }
}

// fondo en el tab 3
.bg_paso_3 {
  background-image: url(/build/img/hap5.webp);
  background-attachment: fixed;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 3rem;
}

.fondo_paso3 {
  background: rgba(95, 68, 94, 0.92);
  ;
  padding: 9rem;
  border-radius: 1rem;

  p,
  h4 {
    color: white;
  }

  .p3 {
    font-weight: 500;
    font-size: 1.7rem;
  }

  .p2 {
    font-size: 1.7rem;
    text-align: center;
  }

  span {
    font-weight: bold;
  }

  .p1 {
    margin-bottom: 2rem;
    font-style: italic;
  }
}

.fondo_paso3 hr {
  margin-bottom: 3rem;
  border: 1px solid #a7a7a7;
}

.hab_seleccion4 {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  margin-top: 5rem;

  a {
    color: white;
    background: #e81656;
    padding: 1rem;
    border-radius: 1rem;

    &:hover {
      background-color: gray;
      transition: .3s ease-in;
    }
  }

}

// econder tabs
.esconder_tabs {
  display: none;
}

// reserva- mq
@media screen and (max-width: 900px) {

  .hab_datos_seccion,
  .part_resumen_reserva {
    grid-template-columns: 100%;
  }

  .hab_seleccion {
    grid-template-columns: 100%;
  }

  .hab_part2 {
    height: auto;
  }

  .contenedor.hab_seleccion3 {
    margin: 0 2rem;
  }

  .contenedor.hab_seleccion3 #hora {
    width: 36%;
  }

  .contenedor.hab_seleccion3 .metodo_pago {
    flex-direction: column;
    gap: 1rem;
  }

  .modal {
    z-index: 21 !important;
  }


  // habitaciones al darle click
  .hab_pag_principal {
    padding: 0px 2rem;
  }

  .cont_hab {
    grid-template-columns: 100%;
  }

  .cont_hab .cont_hab_p .hab_img_text .servicios_imp .elemento_imp .elem_hab_imp_p {
    flex-direction: column;

    p {
      text-align: center;
      font-size: 1.3rem;
    }
  }

  .cont_hab .cont_hab_p .hab_img_text .text_hab_cant p {
    font-size: 1.3rem;
  }

  .cont_hab .cont_hab_p .hab_info_text .hab_info_partes2 {
    flex-direction: column;
  }

  .cont_hab .cont_hab_p .hab_info_text .hab_info_partes .pre_part_info p {
    font-size: 1.6rem;
  }

  .cont_hab .cont_hab_p2 .descrip_reg_hab p:nth-child(2) {

    font-size: 1.4rem;
  }

  .galeria_imagenes {
    grid-template-columns: 50% 50%;
  }

  .galeria_imagenes .f_gal_div p {
    color: #fff;
    font-size: 2rem;
    font-weight: 700;
  }

  .hab_seleccion4 {
    flex-direction: column;
  }

  .fondo_paso3 h4 {
    text-align: center;
  }

  .fondo_paso3 .p2 {
    font-size: 1.7rem;
    text-align: center;
    background: #2b28284d;
    padding: 1rem;
    border-radius: 2rem;
  }

  .cont_hab .cont_hab_p .hab_info_text .hab_info_partes2 .pre_part_info {
    overflow: hidden !important;
  }

  .centro_pag .img_centro_consumo img {
    height: 37rem !important;
  }

  .hab_part2 .info_hab .info_hab_3 .info_parrafo {
  width: 100%;
  } 
.descrip_precio {
  display: flex;
  gap: 1rem;
  flex-direction: column;
}
.precio_habi p {
  text-align: center;
}
}